.listItem {
    background-color: #4B4B4B;
    border-radius: 12px;
    padding: 12px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;
}
.listItemBlock{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.objectName{
    font-size: 1rem;
    color: white;
}
.date{
    background-color: #2A2A2A;
    padding: 15px 20px;
    border-radius: 12px;
    color: white;
    margin-right: 30px;
    border: 1px solid white;
}
.price{
    background-color: #40005D;
    padding: 10px 10px;
    border-radius: 12px;
    border: 2px solid white;
    font-weight: bold;
    color: white;
}
.delete{
   font-size: 2.3rem;
  margin-right: 5px;
}                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                             